* {
}

h1 {
  text-align: center;
  margin-top: 50px;
}

#form {
  display: flex;
  justify-content: center;
  margin: 50px;
}

#table {
  background: rgb(35, 33, 150);
  padding: 50px;
  width: 600px;
  border: 2px solid black;
  font-size: 25px;
  font-weight: 500;
  border-radius: 30px;
  color: wheat;
  /* align-items: baseline; */
}

input {
  font-size: 25px;
  font-weight: 500;
  margin-top: 25px;
}
span {
  color: red;
  font-size: 15px;
  font-weight: 400;
}
#btn {
  background: rgb(18, 94, 18);
  margin-top: 30px;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 400;
  height: 50px;
  width: 200px;
  color: white;
}
#btn:hover {
  background: rgb(169, 12, 12);
}
